import React, { useState } from 'react';
import {
  Box,
  Button,
  Container,
  Grid,
  Paper,
  Typography,
  CircularProgress,
  Alert,
  Snackbar,
  Divider,
} from '@mui/material';
import {
  IconFileText,
  IconFiles,
  IconSearch,
  IconEdit,
  IconDownload,
  IconEye,
} from '@tabler/icons-react';
import FilePickerDialog from '../components/FilePickerDialog';
import { pdfService } from '../services/pdfService';

export default function Dashboard() {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isFilePickerOpen, setIsFilePickerOpen] = useState(false);

  const handleFileSelect = (file: File) => {
    setSelectedFile(file);
    setIsFilePickerOpen(false);
  };

  const handleOperation = async (
    operation: 'compress' | 'merge' | 'edit' | 'plagiarism' | 'view',
    formData: FormData
  ) => {
    setIsLoading(true);
    setError(null);
    try {
      let result: Blob | { plagiarismPercentage: number; matchedSources: string[] };
      switch (operation) {
        case 'compress':
          result = await pdfService.compress(formData);
          if (result instanceof Blob) {
            const url = URL.createObjectURL(result);
            window.open(url, '_blank');
          }
          break;
        case 'merge':
          result = await pdfService.merge(formData);
          if (result instanceof Blob) {
            const url = URL.createObjectURL(result);
            window.open(url, '_blank');
          }
          break;
        case 'edit':
          result = await pdfService.edit(formData);
          if (result instanceof Blob) {
            const url = URL.createObjectURL(result);
            window.open(url, '_blank');
          }
          break;
        case 'plagiarism':
          result = await pdfService.checkPlagiarism(formData);
          if ('plagiarismPercentage' in result) {
            setError(`Plagiarism detected: ${result.plagiarismPercentage}%`);
          }
          break;
        case 'view':
          if (selectedFile) {
            const url = URL.createObjectURL(selectedFile);
            window.open(url, '_blank');
          }
          break;
        default:
          throw new Error('Invalid operation');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setIsLoading(false);
    }
  };

  const handleCompress = async () => {
    if (!selectedFile) return;
    const formData = new FormData();
    formData.append('file', selectedFile);
    await handleOperation('compress', formData);
  };

  const handleMerge = async () => {
    if (!selectedFile) return;
    const formData = new FormData();
    formData.append('files', selectedFile);
    await handleOperation('merge', formData);
  };

  const handleEdit = async () => {
    if (!selectedFile) return;
    const formData = new FormData();
    formData.append('file', selectedFile);
    formData.append('text', 'test text');
    await handleOperation('edit', formData);
  };

  const handlePlagiarism = async () => {
    if (!selectedFile) return;
    const formData = new FormData();
    formData.append('file', selectedFile);
    await handleOperation('plagiarism', formData);
  };

  const handleView = async () => {
    if (!selectedFile) return;
    await handleOperation('view', new FormData());
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" gutterBottom align="center" color="primary">
          PDF Manager Dashboard
        </Typography>
        <Typography variant="subtitle1" gutterBottom align="center" color="text.secondary">
          Professional PDF tools for all your document needs
        </Typography>
      </Box>

      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper
            sx={{
              p: 3,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              bgcolor: 'background.default',
              border: '2px dashed',
              borderColor: 'primary.main',
              borderRadius: 2,
              cursor: 'pointer',
              '&:hover': {
                bgcolor: 'action.hover',
              },
            }}
            onClick={() => setIsFilePickerOpen(true)}
          >
            <IconFileText size={48} color="primary" />
            <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
              {selectedFile ? selectedFile.name : 'Drop PDF file here or click to select'}
            </Typography>
            <Button variant="contained" color="primary" sx={{ mt: 2 }}>
              Select PDF File
            </Button>
          </Paper>
        </Grid>

        <Grid item xs={12}>
          <Divider sx={{ my: 3 }}>
            <Typography variant="h5" color="text.secondary">
              PDF Tools
            </Typography>
          </Divider>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <IconDownload size={24} />
              <Typography variant="h6" sx={{ ml: 1 }}>
                Compress PDF
              </Typography>
            </Box>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2, flexGrow: 1 }}>
              Optimize your PDF file size while maintaining quality
            </Typography>
            <Button
              variant="contained"
              onClick={handleCompress}
              disabled={!selectedFile || isLoading}
              fullWidth
            >
              {isLoading ? <CircularProgress size={24} /> : 'Compress PDF'}
            </Button>
          </Paper>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <IconFiles size={24} />
              <Typography variant="h6" sx={{ ml: 1 }}>
                Merge PDFs
              </Typography>
            </Box>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2, flexGrow: 1 }}>
              Combine multiple PDFs into one document
            </Typography>
            <Button
              variant="contained"
              onClick={handleMerge}
              disabled={!selectedFile || isLoading}
              fullWidth
            >
              {isLoading ? <CircularProgress size={24} /> : 'Merge PDFs'}
            </Button>
          </Paper>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <IconEdit size={24} />
              <Typography variant="h6" sx={{ ml: 1 }}>
                Edit PDF
              </Typography>
            </Box>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2, flexGrow: 1 }}>
              Add or modify text in your PDF
            </Typography>
            <Button
              variant="contained"
              onClick={handleEdit}
              disabled={!selectedFile || isLoading}
              fullWidth
            >
              {isLoading ? <CircularProgress size={24} /> : 'Edit PDF'}
            </Button>
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <IconSearch size={24} />
              <Typography variant="h6" sx={{ ml: 1 }}>
                Check Plagiarism
              </Typography>
            </Box>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2, flexGrow: 1 }}>
              Detect potential plagiarism in your PDF document
            </Typography>
            <Button
              variant="contained"
              onClick={handlePlagiarism}
              disabled={!selectedFile || isLoading}
              fullWidth
            >
              {isLoading ? <CircularProgress size={24} /> : 'Check Plagiarism'}
            </Button>
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <IconEye size={24} />
              <Typography variant="h6" sx={{ ml: 1 }}>
                View PDF
              </Typography>
            </Box>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2, flexGrow: 1 }}>
              Open and view your PDF document in a new tab
            </Typography>
            <Button
              variant="contained"
              onClick={handleView}
              disabled={!selectedFile || isLoading}
              fullWidth
            >
              {isLoading ? <CircularProgress size={24} /> : 'View PDF'}
            </Button>
          </Paper>
        </Grid>
      </Grid>

      <FilePickerDialog
        open={isFilePickerOpen}
        onClose={() => setIsFilePickerOpen(false)}
        onSelect={handleFileSelect}
      />

      <Snackbar
        open={!!error}
        autoHideDuration={6000}
        onClose={() => setError(null)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setError(null)} severity="error" sx={{ width: '100%' }}>
          {error}
        </Alert>
      </Snackbar>
    </Container>
  );
}
