import { axiosInstance } from './axiosInstance';

export const pdfService = {
  async compress(formData: FormData): Promise<Blob> {
    try {
      const response = await axiosInstance.post('/compress', formData, {
        responseType: 'blob',
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      return response.data;
    } catch (error) {
      throw new Error('Failed to compress PDF');
    }
  },

  async merge(formData: FormData): Promise<Blob> {
    try {
      const response = await axiosInstance.post('/merge', formData, {
        responseType: 'blob',
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      return response.data;
    } catch (error) {
      throw new Error('Failed to merge PDFs');
    }
  },

  async edit(formData: FormData): Promise<Blob> {
    try {
      const response = await axiosInstance.post('/edit', formData, {
        responseType: 'blob',
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      return response.data;
    } catch (error) {
      throw new Error('Failed to edit PDF');
    }
  },

  async export(filename: string): Promise<Blob> {
    try {
      const response = await axiosInstance.post(
        '/export',
        { filename },
        {
          responseType: 'blob',
        }
      );
      return response.data;
    } catch (error) {
      throw new Error('Failed to export PDF');
    }
  },

  async checkPlagiarism(
    formData: FormData
  ): Promise<{ plagiarismPercentage: number; matchedSources: string[] }> {
    try {
      const response = await axiosInstance.post('/check-plagiarism', formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });
      return response.data;
    } catch (error) {
      throw new Error('Failed to check plagiarism');
    }
  },
};
